<template>
	<div class="ratingselect">
		<div class="rating-type">
			<span @click="select(2)" class="block positive" :class="{'active':selectType===2}">{{desc.all}}<span class="count">{{ratings.length}}</span></span>
			<span @click="select(0)" class="block positive":class="{'active':selectType===0}">{{desc.positive}}<span class="count">{{positives.length}}</span></span>
			<span @click="select(1)" class="block positive negative":class="{'active':selectType===1}">{{desc.negative}}<span class="count">{{negatives.length}}</span></span>
		</div>
		<div  @click="toggleContente" class="switch" :class="{'on':onlyContent}">
			<span class="iconfont icon">&#xe604;</span>
			<span class="text">只看有内容的评价</span>
		</div>
	</div>
</template>
<script type="text/ecmascript-6">
import { bus } from '../bus.js';
const positive = 0;
const negative = 1;
const all = 2;
export default{
	props:{
		ratings:{
			type:Array,
			default(){
				return [];
			}
		},
		selectType:{
			type:Number,
			default:all
		},
		onlyContent:{
			type:Boolean,
			default:false
		},
		desc:{
			type:Object,
			default(){
				return {
					all:'全部',
					positive:'满意',
					negative:'不满意'
				};
			}
		}
	},
	computed:{
		positives(){
			return this.ratings.filter((rating)=>{//过滤数组中rating.rateType为0的对象 得到一个数组
				return rating.rateType === positive;
			})
		},
		negatives(){
			return this.ratings.filter((rating)=>{
				return rating.rateType === negative;
			})
		}
	},
	methods:{
/*		select(type, event){//传入类型 1.0写法
			if(!event._constructed){
				return
			}
			this.selectType = type;
			this.$dispatch('ratingtype.select',type)
		},*/
		select(type){//传入类型
			//this.selectType = type;
			//this.$dispatch('ratingtype.select',type);//通过监听把值传到父组件1.0
			bus.$emit('select-type', type);//2.0
			console.log(type)
		},
		toggleContente(){
			//this.onlyContent = !this.onlyContent;
			//this.$dispatch('content.toggle',this.onlyContent);
			bus.$emit('only-content', this.onlyContent);//2.0
		}
	}
}
</script>
<style lang="stylus" rel="stylesheet/stylus">
	@import '../../common/fonts/iconfont/iconfont.css'
	.ratingselect
		display:inline-block
		width:100%
		.rating-type
			padding:18px 0
			border-bottom:1px solid rgba(7,17,27,0.1)
			font-size:0
			.block
				display:inline-block
				padding:8px 12px
				margin-right:8px
				border-radius:1px
				color:rgb(77,85,93)
				font-size:12px
				&.active
					color:#fff
				&.positive
					background:rgba(0,160,220,0.2)
					&.active
						background:rgb(0,160,220)
				&.negative
					background:rgba(77,85,93,0.2)
					&.active
						background:rgb(77,85,93)
		.switch
			padding:12px 0
			line-height:24px
			border-bottom:1px solid rgba(7,17,27,0.1)
			color:rgb(147,153,159)
			font-size:0
			.icon
				margin-right:4px
				font-size:24px
				vertical-align:middle
			&.on
				.icon
					color:#00c850
			.text
				font-size:12px
				vertical-align:middle
				
</style>



