<template>
  <div class="seller" ref="sellerScroll">
  	<div class="seller-content">
  		<div class="overview">
  			<h1 class="title">{{seller.name}}</h1>
  			<div class="desc">
  				<star :size="36" :score="seller.score"></star>
  				<span class="text">[{{seller.ratingCount}}]</span>
  				<span class="text">月售{{seller.sellCount}}</span>
  			</div>
  			<ul class="remark">
  				<li class="block">
  					<h2>起送价</h2>
  					<div class="content">
  						<span class="stress">{{seller.minPrice}}元</span>
  					</div>
  				</li>
  				<li class="block">
  					<h2>商家配送</h2>
  					<div class="content">
  						<span class="stress">{{seller.deliveryPrice}}元</span>
  					</div>
  				</li>
  				<li class="block">
  					<h2>平均配送时间</h2>
  					<div class="content">
  						<span class="stress">{{seller.deliveryTime}}分钟</span>
  					</div>
  				</li>
  			</ul>
  			<div class="favorite" @click="faver">
  				<span class="iconfont icon" :class="{'on':favorite}">&#xe72d;</span>
  				<span class="text">{{favoriteText}}</span>
  			</div>
  		</div>
  		<split></split>
  		<div class="bulletin">
  			<h1 class="title">公告与活动</h1>
  			<div class="content-wrapper">
  				<p class="content">{{seller.bulletin}}</p>
  			</div>
		    <ul v-if="seller.supports" class="supports">
		      <li v-for="(item , index) in seller.supports" class="support-item">
		        <span class="icon" :class="classMap[seller.supports[index].type]"></span>
		        <span class="text">{{seller.supports[index].description}}</span>
		      </li>
		    </ul>
  		</div>
  		<split></split>
  		<div class="pics">
  			<h1 class="title">商家实景</h1>
  			<div class="pic-wrapper" ref="picList">
  				<ul class="pic-list">
  					<li class="pic-item" v-for="pic in seller.pics">
  						<img :src="pic" width="120" height="90" alt="">
  					</li>
  				</ul>
  			</div>
  		</div>
  		<split></split>
  		<div class="info">
  			<h1 class="title">商家信息</h1>
  			<ul>
  				<li class="info-item" v-for="info in seller.infos">{{info}}</li>
  			</ul>
  		</div>
  	</div>
  </div>
</template>
<script type="text/ecmascript-6">
import BScoll from 'better-scroll';
import star from '../../components/star/star.vue';
import split from '../../components/split/split.vue';
const local = 0;
  export default {
  	props:{
  		seller:{
  			type:Object
  		}
  	},
  	data(){
  		return{
  			favorite:false
  		}
  	},computed:{
  		favoriteText(){
  			console.log(this.favorite+"=="+1)
  			return this.favorite ? '已收藏' : '收藏';
  		}
  	},
  	created() {
      this.classMap = ['decrease','discount','guarantee','invoice','special'];
	  this.$nextTick(() =>{
	  	this._initScroll && this._initScroll();
	  	this._initPic && this._initPic();
	  	this.getItem()
	  });
    },
    methods:{
    	_initScroll(){
			if(!this.scroll){//如果没有才创建
				this.sellerScroll = new BScoll(this.$refs.sellerScroll,{
				click: true//允许浏览器默认事件 默认是阻止
			})
			} else {
				this.sellerScroll.refresh();//有的话就调用接口刷新
			}
		},
    	_initPic (){
	    	if(this.seller.pics){//横向滚动
	    		let picWidth = 120;
	    		let margin = 6;
	    		let width = (picWidth + margin) * this.seller.pics.length - margin;
	    		//console.log(this.$refs.picList.childNodes)
	    		this.$refs.picList.childNodes[0].style.width = width + 'px';
	    		this.$nextTick(()=>{
	    			this.picScroll = new BScoll(this.$refs.picList,{
					scrollX: true,//允许浏览器默认事件 默认是阻止
					eventnPassthrough:'vertical'
				})
	    		})
	    	}
		},
		faver(){
			this.favorite = !this.favorite;
			localStorage.setItem('faver',this.favorite);
		},
		getItem(){
			if(localStorage){
				this.favorite = JSON.parse(localStorage.getItem('faver'));
			}
		}
    },
  	components:{
  		star,
  		split,
  		BScoll
  	}
  };
</script>
<style lang="stylus" rel="stylesheet/stylus">
	@import "../../common/fonts/iconfont/iconfont.css"
	@import "../../common/stylus/minsty.styl"
	.seller
		position:absolute
		top:174px
		bottom:0
		left:0
		width:100%
		overflow:hidden
		.overview
			position:relative
			padding:18px
			.title
				margin-bottom:8px
				line-height:14px
				color:rgb(7,17,27)
				font-size:14px
			.desc
				padding-bottom:18px
				border-bottom:1px solid #ddd
				font-size:0
				.star
					display:inline-block
					margin-right:8px
					vertical-align:top
				.text
					display:inline-block
					margin-right:12px
					vertical-align:top
					font-size:10px
					color:rgb(77,85,93)
					line-height:18px
			.remark
				display:flex
				padding-top:18px
				.block
					flex:1
					text-align:center
					border-right:1px solid rgba(7,17,27,0.1)
					&:last-child
						border:none
					h2
						margin-bottom:4px
						line-height:10px
						font-size:10px
						color:rgb(147,153,159)
					.content
						line-height:24px
						font-size:10px
						color:rgb(7,17,27)
						.stress
							font-size:24px
			.favorite
				position:absolute
				width:50px
				right:11px
				top:18px
				text-align:center
				.icon
					display:block
					font-size:24px
					color:#ddd
					&.on
						color:rgb(240,20,20)
				.text
					font-size:10px
					color:rgb(77,85,93)
					margin-top:4px
		.bulletin
			padding:18px 18px 0 18px
			.title
				margin-bottom:8px
				line-height:14px
				color:rgb(7,17,27)
				font-size:14px
			.content-wrapper
				padding:0 12px 16px 12px
				border-bottom:1px solid #ddd
				.content
					line-height:24px
					font-size:12px
					color:rgb(240,20,20)
			.supports
				.support-item
					padding:16px 12px
					border-bottom:1px solid #ddd
					font-size:0
					&:last-child
						border:none
					.icon
						display:inline-block
						width:16px
						height:16px
						vertical-align:middle
						background-size:16px 16px !important
						background-repeat:no-repeat
						margin-right:6px
						&.decrease
							bg-image('decrease_4')
						&.discount
							bg-image('discount_4')
						&.guarantee
							bg-image('guarantee_4')
						&.invoice
							bg-image('invoice_4')
						&.special
							bg-image('special_4')
					.text
						vertical-align:middle
						line-height:16px
						font-size:12px
						color:rgb(7,17,27)
		.pics
			padding:18px
			.title
				margin-bottom:12px
				line-height:14px
				color:rgb(7,17,27)
				font-size:14px
			.pic-wrapper
				width:100%
				overflow:hidden
				white-space:nowrap
				.pic-list
					font-size:0
					.pic-item
						display:inline-block
						margin-right:6px
						width:120px
						height:90px
						&:last-child
							margin:0
		.info
			padding:18px 18px 0 18px
			color:rgb(7,17,27)
			.title
				padding-bottom:12px
				line-height:14px
				border-bottom:1px solid #ddd
				font-size:14px
			.info-item
				padding:16px 12px
				border-bottom:1px solid #ddd
				font-size:12px
				&:last-child
					border:none
</style>



