<template>
	<div class="shopcart">
		<div class="content" @click="toggleList">
			<div class="content-left">
				<div class="logo-wrapper">
					<div class="logo" :class="{'on':totalNum>0}" >
						<span class="icon-shop-cart">
						<i class='iconfont'>&#xe601;</i></span>
					</div>
					<span class="num" v-show="totalNum>0">{{totalNum}}</span>
				</div>
				<div class="price" :class="{'on':totalPrice>0}">{{totalPrice}}元</div>
				<div class="desc">另需配送费￥{{deliveryPrice}}</div>
			</div>
			<div class="content-right" @click.stop.prevent="pay">
				<div class="pay" :class="{'on':payDesc==='结算'}">{{payDesc}}</div>
			</div>
		</div>
		<transition name="fade">
		<div class="shopcart-list" v-show="listShow" >
			<div class="list-header">
				<h1 class="title">购物车</h1>
				<span class="empty" @click="empty">清空</span>
			</div>
			<div class="list-content" ref="listContent">
				<ul>
					<li class="food" v-for="food in selectFoods">
						<span class="name">{{food.name}}</span>
						<span class="price">￥{{food.price*food.count}}</span>
						<div class="cartcontrol-wrapper">
							<cartcontrol :food="food"></cartcontrol>
						</div>
					</li>
				</ul>
			</div>
		</div>
		</transition>
	<transition name="show">
	<div class="list-mask" v-show="listShow" @click="hideList"></div>
	</transition>
	</div>
</template>
<script type="text/ecmascript-6">
import BScoll from 'better-scroll';
import cartcontrol from '../../components/cartcontrol/cartcontrol.vue';
export default{
	props: {//通过父组件传过来的三个值
		selectFoods:{//添加的食品价格和数量
			type:Array
		},
		deliveryPrice:{//配送费
			type:Number,
			default:0
		},
		minPrice:{//起送费
			type:Number,
			default:0
		}
	},
	data(){
		return{
			fold:true
		}
	},
	computed:{
		totalPrice(){//计算商品总价
			let total = 0;
			this.selectFoods.forEach((food)=>{
				total += food.price * food.count;
			});
			return total;
		},
		totalNum(){//计算商品数量
			let num = 0;
			this.selectFoods.forEach((food)=>{
				num += food.count;
			});
			return num;
		},
		payDesc(){//定义多少起送的方法
			if(this.totalPrice === 0){
				return `${this.minPrice}元起送`;
			}else if(this.totalPrice<this.minPrice){//当配送费少于最少配送费的时候
				let diff = this.minPrice - this.totalPrice;
				return `还差${diff}元起送`;
			}else{
				return '结算';
			}
		},
		listShow(){
			if(!this.totalNum){
				this.fold = true;
				return false;
			}
			let show = !this.fold;
			if(show){
				this.$nextTick(()=>{
					if(!this.scroll){//如果没有才创建
						this.scroll = new BScoll(this.$refs.listContent,{
						click: true//允许浏览器默认事件 默认是阻止
					})
					} else {
						this.scroll.refresh();//有的话就调用接口刷新
					}
				})
			}
			return show;
		}
	},
	methods:{
		toggleList(){
			console.log(this.totalNum+"=="+this.fold)
			if(!this.totalNum){
				return;
			}
			this.fold = !this.fold;
		},
		empty(){
			this.selectFoods.forEach((food)=>{
				food.count = 0;
			})
		},
		hideList(){
			this.fold = true;
		},
		pay(){
			if(this.totalPrice<this.minPrice){
				return
			}else{
				window.alert(`支付${this.totalPrice}`)
			}
		}
	},
	components:{
		cartcontrol
	}
}
</script>
<style src="../../common/fonts/iconfont/iconfont.css"></style>
<style lang="stylus" rel="stylesheet/stylus">
	@import "../../common/stylus/minsty"
	.shopcart
		position:fixed
		left:0
		bottom:0
		z-index:9
		width:100%
		height:48px
		.content
			display:flex
			background:#141d27
			font-size:0
			color:rgba(255,255,255,0.4)
			.content-left
				flex:1
				.logo-wrapper
					display:inline-block
					position:relative
					top:-10px
					margin:0 12px
					padding:6px
					width:45px
					height:45px
					border-sizing:border-box
					vertical-align:top
					border-radius:50%
					background:#141d27
					.logo
						width:100%
						height:100%
						border-radius:50%
						background:#2b343c
						text-align:center
						color:#80858a
						&.on
							background:#03A9F4
						.icon-shop-cart
							line-height:44px
							font-size:24px
							color:#fff
							& > i
								font-size:28px
								line-height:45px
					.num
						position:absolute
						top:0
						right:0
						width:24px
						height:16px
						line-height:16px
						text-align:center
						border-radius:8px
						font-size:9px
						font-weight:700
						color:#fff
						background:#69c77a
				.price
					display:inline-block
					vertical-align:top
					margin-top:12px
					line-height:24px
					padding-right:12px
					box-sizing:border-box
					border-right:1px solid rgba(255,255,255,0.1)
					font-size:16px
					font-weight:700
					@media only screen and (max-width:320px)
						font-size:10px
					&.on
						color:#fff
				.desc
					display:inline-block
					vertical-align:top
					margin:12px 0 0 12px
					line-height:24px
					font-size:16px
					@media only screen and (max-width:320px)
						font-size:10px
						margin:12px 0 0 1px
			.content-right
				flex:0 0 105px
				background:rgba(255,255,255,0.4)
				.pay
					height:48px
					line-height:48px
					text-align:center
					font-size:12px
					display:block
					&.on
						background:#69c77a
		.shopcart-list
			position:absolute
			left:0
			top:0
			width:100%
			z-index:-1
			transform:translate3d(0,-100%,0)
			background:#464
			transition:0.3s all
			&.fade-enter-active, &.fade-leave-active
				transform: translate3d(0,0,0)
				z-index:-1
         	.list-header
	        	height:40px
	        	line-height:40px
	        	padding:0 18px
	        	background:#f3f5f7
	        	border-bottom:1px solid #ddd
	.title
		float:left
		font-size:14px
		color:rgb(7,17,27)
	.empty
		float:right
		font-size:12px
		color:rgb(0,160,220)
.list-content
	padding:0 18px
	max-height:217px
	background:#fff
	overflow:hidden
	.food
		position:relative
		padding:12px 0
		box-sizing:border-box
		border-bottom:1px solid rgba(7,17,27,0.1)
		.name
			line-height:24px
			font-size:14px
			color:rgb(7,17,27)
		.price
			position:absolute
			right:90px
			bottom:12px
			line-height:24px
			font-size:14px
			font-weight:700
			color:rgb(240,20,20)
		.cartcontrol-wrapper
			width:80px
			position:absolute
			right:0
			bottom:10px
.list-mask
	position:fixed
	top:0
	left:0
	width:100%
	height:100%
	z-index:-2
	transition:0.3s all
	opacity:1
	background:rgba(7,17,27,0.6)
	&.show-enter-active, &.show-leave-active
		opacity:0
</style>



