<template>
  <div class="ratings" ref="ratingWrapper">
  	<div class="scroll-wrapper">
	  	<div class="ratings-content">
	  		<div class="overview">
	  			<div class="overview-left">
	  				<h1 class="score">{{seller.score}}</h1>
	  				<div class="title">综合评分</div>
	  				<div class="rank">高于周围商家{{seller.rankRate}}%</div>
	  			</div>
	  			<div class="overview-right">
	  				<div class="score-wrapper">
	  					<span class="title">服务态度</span>
						<star :size="36" :score="seller.serviceScore"></star>
						<span class="score">{{seller.serviceScore}}</span>
	  				</div>
	  				<div class="score-wrapper">
	   					<span class="title">商品评分</span>
						<star :size="36" :score="seller.foodScore"></star>
						<span class="score">{{seller.foodScore}}</span>
	  				</div>
	  				<div class="delivery-wrapper">
	  					<span class="title">送达时间</span>
	  					<span class="delivery">{{seller.deliveryTime}}分钟</span>
	  				</div>
	  			</div>
	  		</div>
	  	</div>
	  	<split></split>
	  	<ratingselect  :selectType="selectType" :onlyContent="onlyContent" :desc="desc" :ratings="ratings"></ratingselect>
	  	<div class="rating-wrapper">
	  		<ul>
	  			<li v-for="rating in ratings" v-show="needShow(rating.rateType,rating.text)"  class="rating-item">
	  				<div class="avatar">
	  					<img width="28" height="28" :src="rating.avatar" alt="">
	  				</div>
	  				<div class="content">
	  					<h1 class="name">{{rating.username}}</h1>
	  					<div class="star-wrapper">
	  						<star :size="24" :score="rating.score"></star>
	  						<span class="delivery" v-show="rating.deliveryTime"></span>
	  					</div>
	  					<p class="text">{{rating.text}}</p>
	  					<div class="recommend" v-show="rating.recommend && rating.recommend.length">
	  						<span class="icon-up"><i class="iconfont">&#xe613;</i></span>
	  						<span class="item" v-for="item in rating.recommend">{{item}}</span>
	  					</div>
	  					<div class="time">{{rating.rateTime | formatDate}}</div>
	  				</div>
	  			</li>
	  		</ul>
	  	</div>
  	</div>
  </div>
</template>
<script type="text/ecmascript-6">
import { bus } from '../bus.js'
import BScoll from 'better-scroll';
import {formatDate} from '../../common/js/data.js';
import star from '../../components/star/star.vue';
import split from '../../components/split/split.vue';
import ratingselect from '../../components/ratingselect/ratingselect.vue';
const all = 2;
export default {
	props:{
		seller:{
			type:Object
		}
	},data(){
		return{
			ratings:[],
			selectType:all,
			onlyContent:false,
			desc:{
				all:'全部',
				positive:'满意',
				negative:'不满意'
			}
		}
	},
	created(){
		this.$http.get('/ratings').then((res)=>{
		res = res.body;
		if(res.errno === 0){
		  this.ratings = res.data
		  this.$nextTick(() =>{
		  	this._initScroll && this._initScroll();
		  	//this._culculateHeight();
		  });
		  //console.log(this.goods)
		}
	  }),
		bus.$on('select-type',(type)=>{
			this.selectType = type;
			this.$nextTick(()=>{
				this.ratingScroll.refresh()
			})
		}),
		bus.$on('only-content',(onlyContent)=>{
			this.onlyContent = !onlyContent;
			this.$nextTick(()=>{
				this.ratingScroll.refresh()
			})
		})
	},
	methods:{
		_initScroll(){
			this.$nextTick(()=>{
				if(!this.scroll){//如果没有才创建
					this.ratingScroll = new BScoll(this.$refs.ratingWrapper,{
					click: true//允许浏览器默认事件 默认是阻止
				})
				} else {
					this.ratingScroll.refresh();//有的话就调用接口刷新
				}
				console.log(this.ratingScroll)
			})
		},
		needShow(type,text){
			if(this.onlyContent && !text){
				return false;
			}
			if(this.selectType === all){
				return true
			}else{
				return type === this.selectType;
			}
		},
	},
	filters:{
		formatDate(time){
			let date = new Date(time);
			return formatDate(date,'yyyy-MM-dd hh:mm')
		}
	},
	components:{
		star,
		split,
		ratingselect
	}
};
</script>
<style lang="stylus" rel="stylesheet/stylus">
@import "../../common/fonts/iconfont/iconfont.css"
.ratings
	position:absolute
	top:174px
	bottom:0
	left:0
	width:100%
	overflow:hidden
	.overview
		display:flex
		padding:18px 0
		.overview-left
			flex:0 0 137px
			padding:6px 0
			width:137px
			border-right:1px solid rgba(7,17,27,0.1)
			text-align:center
			@media only screen and (max-width:320px)
				flex:0 0 120px
				width:120px
			.score
				line-height:28px
				font-size:24px
				color:rgb(255,153,0)
			.title
				line-height:12px
				font-size:12px
				color:rgb(7,17,27)
				margin:6px 0 8px 0
			.rank
				line-height:10px
				font-size:10px
				color:rgb(147,153,159)
		.overview-right
			flex:1
			padding:6px 0 6px 24px
			@media only screen and (max-width:320px)
				padding-left:6px
			.score-wrapper
				margin-bottom:8px
				line-height:18px
				font-size:0
				.title
					font-size:12px
					color:rgb(7,17,27)
					vertical-align:top
				.star
					display:inline-block
					vertical-align:top
					margin:0 12px
					@media only screen and (max-width:320px)
						margin:0 8px
				.score
					display:inline-block
					vertical-align:top
					font-size:12px
					color:rgb(255,153,0)
			.delivery-wrapper
				font-size:0
				.title
					font-size:12px
					color:rgb(7,17,27)
					vertical-align:top
				.delivery
					font-size:12px
					color:rgb(147,153,159)
					margin-left:12px
					@media only screen and (max-width:320px)
						margin-left:8px
	.ratingselect
		padding:18px
	.rating-wrapper
		padding:0 18px
		.rating-item
			display:flex
			padding:18px 0
			border-bottom:1px solid #ddd
			.avatar
				flex:0 0 28px
				width:28px
				margin-right:12px
				img
					border-radius:50%
			.content
				position:relative
				flex:1
				.name
					margin-bottom:4px
					line-height:12px
					font-size:10px
					color:rgb(7,17,27)
				.star-wrapper
					margin-bottom:6px
					font-size:0
					.star
						display:inline-block
						margin-right:6px
						vertical-align:top
					.delivery
						font-size:12px
						color:rgb(147,153,159)
						margin-left:12px
						color:rgb(7,17,27)
				.text
					margin-bottom:18px
					line-height:18px
					color:rgb(7,17,27)
					font-size:12px
				.recommend
					line-height:16px
					font-size:0
					.icon-up, .item
						display:inline-block
						margin:0 8px 4px 0
						font-size:9px
					.icon-up
						color:rgb(0,160,220)
						vertical-align:top
					.item
						padding:0 6px
						border:1px solid rgba(7,17,27,0.1)
						border-radius:1px
						color:rgb(147,153,159)
						background:#fff
						max-width:50px
						white-space:nowrap
						overflow:hidden
						text-overflow:ellipsis
						vertical-align:top
				.time
					position:absolute
					top:0
					right:0
					line-height:12px
					font-size:10px
					color:rgb(147,153,159)
</style>



