<template>
	<div class="cartcontrol">
		<div class="crat-decrease" v-show="food.count>0" @click.stop.prevent="subCart"><i class="iconfont">&#xe602;</i></div>
		<div class="cart-count" v-show="food.count>0">{{food.count}}</div>
		<div class="cart-add" @click.stop.prevent="addCart"><i class="iconfont">&#xe603;</i></div>
	</div>
</template>
<script type="text/ecmascript-6">
import Vue from 'vue';
export default{
	props:{
		food:{
			type:Object
		}
	},
	methods:{
		addCart(){
			if(!this.food.count){
				Vue.set(this.food,'count',1);//使用vue组件来建立一个不存在的属性才可以实时更新
				//this.food.count = 1
			}else{
				this.food.count++
			}
		},
		subCart(){
			if(this.food.count>0){
				this.food.count--
			}
		}
	}
}
</script>
<style src="../../common/fonts/iconfont/iconfont.css"></style>
<style lang="stylus" rel="stylesheet/stylus">
	.cartcontrol
		font-size:0
		position:relative
		i
			font-size:24px
			color:#00A0DC
			padding:4px
		.crat-decrease
			display:inline-block
			position:absolute
			right:left
			transform:translate3d(0,0,0)
			& > i
				font-size:21px 
				line-height:24px
		.cart-count
			display:inline-block
			text-align:center
			width:16px
			color:rgb(147,153,159)
			font-size:10px
			line-height:24px
			margin-left:30px
		.cart-add
			display:inline-block
			font-size:24px
			position:absolute
			right:0
</style>



