<template>
	<transition name="fade">
	<div v-show="showFlag" class="food" ref="food">
		<div class="food-content">
			<div class="img-header">
				<img :src="food.image" alt="">
				<div class="back" @click="back">
					<span>&lt</span>
				</div>
			</div>
			<div class="content">
				<h1 class="title">{{food.name}}</h1>
				<div class="detail">
					<span class="sell-count">月售{{food.sellCount}}份</span>
					<span class="rating">好评{{food.rating}}</span>
				</div>
				<div class="price">
					<span class="now">￥{{food.price}}</span>
					<s class="old" v-show="food.oldPrice">￥{{food.oldPrice}}</s>
				</div>
				<div class="cartcontrol-wrapper">
					<cartcontrol :food="food" v-show="food.count>0"></cartcontrol>
				</div>
				<div @click="addFirst" class="buy" v-show="!food.count || food.count===0">加入购物车</div>
			</div>
			<split v-show="food.info"></split>
			<div class="info" v-show="food.info">
				<h1 class="title">商品信息</h1>
				<p class="text">{{food.info}}</p>
			</div>
			<split></split>
			<div class="rating">
				<h1 class="title">商品评价</h1>
				<ratingselect  :selectType="selectType" :onlyContent="onlyContent" :desc="desc" :ratings="food.ratings"></ratingselect>
			</div>
			<div class="rating-wrapper">
				<ul v-show="food.ratings && food.ratings.length">
					<li v-show="needShow(rating.rateType,rating.text)" v-for="rating in food.ratings" class="rating-item">
						<div class="user">
							<span class="name">{{rating.username}}</span>
							<img class="avatar" width="12" height="12" :src="rating.avatar" alt="">
						</div>
						<div class="time">{{rating.rateTime | formatDate}}</div>
						<p class="text">
							<span v-show="rating.rateType===0" class="icon-up iconfont">&#xe613;</span>
							<span v-show="rating.rateType===1" class="icon-down iconfont">&#xe605;</span>
							{{rating.text}}
						</p>
					</li>
				</ul>
				<div class="no-rating" v-show="!food.ratings || !food.ratings.length">暂无评价</div>
			</div>
		</div>
	</div>
	</transition>
</template>
<script type="text/ecmascript-6">
import { bus } from '../bus.js'
import BScoll from 'better-scroll';
import {formatDate} from '../../common/js/data.js';
import Vue from 'vue';
import cartcontrol from '../../components/cartcontrol/cartcontrol.vue';
import split from '../../components/split/split.vue';
import ratingselect from '../../components/ratingselect/ratingselect.vue';
const positive = 0;
const negative = 1;
const all = 2;
export default{
	props:{
		food:{
			type:Object
		}
	},
	data(){
		return{
			showFlag:false,
			selectType:all,
			onlyContent:false,
			desc:{
				all:'全部',
				positive:'推荐',
				negative:'吐槽'
			}
		}
	},
	created(){
		bus.$on('select-type',(type)=>{
			this.selectType = type;
			this.$nextTick(()=>{
				this.scroll.refresh()
			})
		}),
		bus.$on('only-content',(onlyContent)=>{
			this.onlyContent = !onlyContent;
			this.$nextTick(()=>{
				this.scroll.refresh()
			})
		})
	},
	methods:{
		show(){
			this.showFlag = true;
			this.selectType = positive;
			this.onlyContent = false;
				this.$nextTick(()=>{
					if(!this.scroll){//如果没有才创建
						this.scroll = new BScoll(this.$refs.food,{
						click: true//允许浏览器默认事件 默认是阻止
					})
					} else {
						this.scroll.refresh();//有的话就调用接口刷新
					}
				})
		},
		back(){
			this.showFlag = false;
		},
		addFirst(){
			Vue.set(this.food,'count',1)//第一次会把count添加为1
		},
		needShow(type,text){
			if(this.onlyContent && !text){
				return false;
			}
			if(this.selectType === all){
				return true
			}else{
				return type === this.selectType;
			}
		},
	},
	filters:{
		formatDate(time){
			let date = new Date(time);
			return formatDate(date,'yyyy-MM-dd hh:mm')
		}
	},
	components:{
		cartcontrol,
		split,
		ratingselect
	}
};
</script>
<style lang="stylus" rel="stylesheet/stylus">
	.food
		position:fixed
		left:0
		top:0
		bottom:48px
		width:100%
		transform: translate3d(0,0,0)
		transition:0.5s all
		background:#fff
		&.fade-enter-active, &.fade-leave-active
			transform: translate3d(100%,0,0)
		.img-header
			position:relative
			width:100%
			height:0
			padding-top:100%/*这里的padding-top 100 == width*/
			img
				position:absolute
				top:0
				left:0
				width:100%
				height:100%
			.back
				position:absolute
				top:10px
				left:0
				span
					font-family: cursive
					display:block
					padding:10px
					font-size:20px
					color:#fff
		.content
			position:relative
			padding:18px
			.title
				line-height:14px
				margin-bottom:8px
				font-size:14px
				font-weight:700
				color:rgb(7,17,27)
			.detail
				margin-bottom:18px
				line-height:10px
				font-size:0
				.sell-count , .rating
					font-size:10px
					color:rgb(147,153,159)
				.sell-count
					margin-right:12px
			.price
				font-weight:700
				line-height:24px
				.now
					margin-right:8px
					font-size:14px
					color:rgb(240,20,20)
				.old
					text-decoration:line-throungh
					font-size:10px
			.cartcontrol-wrapper
				position:absolute
				right:12px
				bottom:12px
				width:80px
			.buy
				position:absolute
				right:18px
				bottom:18px
				z-index:10
				height:24px
				line-height:24px
				padding:0 12px
				box-sizing:border-box
				font-size:10px
				border-radius:12px
				color:#fff
				background:rgb(0,160,220)
		.info
			padding:18px
			.title
				line-height:14px
				margin-bottom:6px
				font-size:14px
				color:rgb(7,17,28)
			.text
				line-height:24px
				padding:0 8px
				font-size:12px
				color:rgb(77,85,93)
		.rating
			padding:18px
			.title
				line-height:14px
				margin-bottom:6px
				font-size:14px
				color:rgb(7,17,27)
		.rating-wrapper
			padding:0 18px
			.rating-item
				position:relative
				padding:16px 0
				border-bottom:1px solid #ddd
				.user
					position:absolute
					right:0
					top:16px
					line-height:12px
					font-size:0
					.name
						display:inline-block
						margin-right:6px
						vertical-align:middle
						font-size:10px
						color:rgb(147,153,159)
					.avatar
						border-radius:50%
						vertical-align:middle
				.time
					margin-bottom:6px
					line-height:12px
					font-size:10px
					color:rgb(147,153,159)
				.text
					line-height:16px
					font-size:12px
					color:rgb(7,17,27)
					span
						margin-right:4px
						line-height:16px
						font-size:18px
						&.icon-up
							color:rgb(0,160,220)
						&.icon-down
							color:rgb(147,153,159)
			.no-rating
				padding:16px 0
				font-size:12px
				color:rgb(147,153,159)
</style>



